<template >
  <div id="app">
    <div >
      <Sidebar
      v-click-outside="closeMenu"
        id="sidebar"
        v-bind:size="size"
        v-bind:showSidebar="showSidebar"
        :showMenu="showMenu"
      />
    </div>
    <Content id="content" v-bind:size="size" />
  </div>
</template>

<script>
import { Sidebar } from "./components/Sidebar";
import { Content } from "./components/Content";
export default {
  name: "app",
  components: {
    Sidebar,
    Content
  },
  data: function() {
    return {
      size: { isSm: true, isMd: false, isLg: false },
      showSidebar: false,
      window:{width:0,height:0}
    };
  },
  created:function() {
    window.addEventListener('resize', this.handleResize)
    this.handleResize();
  },
  destroyed:function() {
    window.removeEventListener('resize', this.handleResize)
  },
  methods: {
    showMenu: function() {
      console.log("Click en Menu!"), (this.showSidebar = true);
    },
    closeMenu: function() {
      console.log("Click fuera del menu"), (this.showSidebar = false);
    },
    handleResize() {
      this.window.width = window.innerWidth;
      this.window.height = window.innerHeight;
      if(this.window.width > 798 ) {
        this.size.isLg = true
        this.size.isMd = false
        this.size.isSm = false
      }
      if(this.window.width >= 400 && this.window.width < 798 ){
        this.size.isLg = false
        this.size.isMd = true
        this.size.isSm = false
      }
      if(this.window.width < 400){
        this.size.isLg = false
        this.size.isMd = false
        this.size.isSm = true
      }
    }
  },
  directives: {
    "click-outside": {
      bind: function(el, binding, vNode) {
        // Provided expression must evaluate to a function.
        if (typeof binding.value !== "function") {
          const compName = vNode.context.name;
          let warn = `[Vue-click-outside:] provided expression '${binding.expression}' is not a function, but has to be`;
          if (compName) {
            warn += `Found in component '${compName}'`;
          }

          console.warn(warn);
        }
        // Define Handler and cache it on the element
        const bubble = binding.modifiers.bubble;
        const handler = e => {
          if (bubble || (!el.contains(e.target) && el !== e.target)) {
            binding.value(e);
          }
        };
        el.__vueClickOutside__ = handler;

        // add Event Listeners
        document.addEventListener("click", handler);
      },

      unbind: function(el, binding) {
        // Remove Event Listeners
        document.removeEventListener("click", el.__vueClickOutside__);
        el.__vueClickOutside__ = null;
      }
    }
  }
};
</script>

